import {
    fetchPreviewDataError, 
    fetchPreviewDataPending, 
    fetchPreviewDataSuccess, 
    getConfigPending,
    getConfigSuccess,
    getConfigError,
    saveDataError, 
    saveDataPending, 
    saveDataSuccess,
    getDevCustomStore
} from './actions';

import _ from 'lodash';

import {
    formulasLib, getUserToken, SDKNotification, ErrorsWindow
} from '../Utils'

import CustomStore from 'devextreme/data/custom_store';

export const save = (close_window = false) => {
    return (dispatch, getState) => {
        let current_state = getState().general || {};
        dispatch(saveDataPending());
        fetch('/api/BLFlex/SaveReport/', {
            method: 'POST',
            credentials: "same-origin",
            headers: {
                "Accept": "application/json",
                'X-Requested-With': 'XMLHttpRequest'
            },
            body: JSON.stringify({
                "id": current_state.id,
                "name": current_state.report_header_data.name,
                "description": current_state.report_header_data.description,
                "selected_class": current_state.report_header_data.selected_class,
                "module":  current_state.report_header_data.module,
                "visible_users": current_state.report_header_data.visible_users,
                "visible_roles": current_state.report_header_data.visible_roles,
                "visible_on_module": current_state.report_header_data.visible_on_module,
                "module_path": current_state.report_header_data.module_path,
                "select_list": current_state.columns,
                "filter_list": current_state.filters,
                "chart_list": current_state.chart_list,
                "owner": { id: current_state.report_header_data.owner_id },
                "owner_team": { id: current_state.report_header_data.owner_team_id },
                "group_list": current_state.groups,
                "version": 2,
                "metadata": JSON.stringify(Object.assign(current_state.report_header_data.metadata || {}, {
                    field_list: current_state.field_list,
                })),
                "is_list_view": current_state.is_list_view,
                "bl_name_list": current_state.bl_name_list,
                "columns_changed": current_state.columns_changed,
                'viewdef_name': current_state.viewdef_name,
                'delta_old': current_state.report_header_data.delta,
            })
        })
            .then(res => res.json())
            .then(res => {
                if (res.status === false) {
                    throw (res.message);
                }
                dispatch(saveDataSuccess(res.data));
                SDKNotification.ShowSuccess("BLFlex.Notification.SuccessSaveData");
                if (close_window) {
                    let new_url = '/BLFlex/detail/' + res.data.id;
                    if(window.SDKDotNetHelper != null && typeof window.SDKDotNetHelper != "undefined"){
                        window.SDKDotNetHelper.invokeMethodAsync("NavigateTo", new_url);
                    }else{
                        window.location.href = new_url;
                    }
                }
                ErrorsWindow.SuccessFlex();
                return res.data;
            })
            .catch(error => {
                try {
                    console.error(error);
                    ErrorsWindow.ErrorsFlex(error);
                    SDKNotification.ShowError("Custom.Generic.Message.Error");
                } catch (err) {
                    console.error(err);
                    SDKNotification.ShowError("BLFlex.Notification.ErrorSave");
                    ErrorsWindow.ErrorsFlex(err);
                }
                dispatch(saveDataError(error));
            })
    };
}

export const fetchMainConfig = () => {
    return (dispatch, getState) => {
        let current_state = getState().general || {};
        let url = '/api/BLFlex/GetConfig/';
        dispatch(getConfigPending());
        fetch(url, {
            method: 'POST',
            credentials: "same-origin",
            headers: {
                "Accept": "application/json",
                'X-Requested-With': 'XMLHttpRequest'
            },
            body: JSON.stringify({
                "id": current_state.id || 0,
                "bl_name_list": current_state.bl_name_list || '',
                "viewdef_name": current_state.viewdef_name || '',
                "flex_filters": current_state.flex_filters || '',
            })
        })
            .then(res => res.json())
            .then(res => {
                if (res.status === false) {
                    throw (res.message);
                }
                //console.log(res.data);
                //check if res.data.metadata is a string or object
                try {
                    if (typeof res.data.report_header_data.metadata === 'string') {
                        res.data.report_header_data.metadata = JSON.parse(res.data.report_header_data.metadata);
                    }
                } catch (error) {
                    console.log(error);
                    ErrorsWindow.ErrorsFlex(error);
                }
                
                dispatch(getConfigSuccess(res.data));
                dispatch(fetchPreviewData());
                ErrorsWindow.SuccessFlex();
                return res.data;
            })
            .catch(error => {
                try {
                    console.error(error);                    
                    SDKNotification.ShowError("Custom.Generic.Message.Error");
                    ErrorsWindow.ErrorsFlex(error);
                } catch (err) {
                    console.error(err);                                        
                    SDKNotification.ShowError("Custom.Generic.Message.Error");
                    ErrorsWindow.ErrorsFlex(err);
                }
                dispatch(getConfigError(error));
            });
    }
}

export const fetchData = (url, params, current_state, dispatch, data_always) => {
    return fetch(url, params)
    .then(res => res.json())
    .then(res => {
        if (res.status === false) {
            throw (res.message);
        }
        if (res.data.length == 0) {
            if(dispatch != null){
                dispatch(fetchPreviewDataSuccess(res.data));
            }            
            return res.data;
        }
        data_always = data_always || false;
        if(res.data.length == 1 && current_state.has_search && !data_always && !current_state.from_entity_field){
            let data = res.data[0];
            let blName = current_state.bl_name_list;
            let url = blName + '/detail/' + data.rowid;
            window.SDKDotNetHelper.invokeMethodAsync("NavigateTo", url);
            return res.data;
          }
        const fieldsMapTypes = current_state.field_list[0].options.map(option => Object.assign({}, { [option.name]: option.type })).reduce((obj, curr) => {
            return { ...obj, ...curr }
        }, {})
        const formulaColumns = current_state.columns.filter(col => col.customFn).concat(current_state.groups.filter(col => col.customFn))
        const enumColumns = current_state.columns.filter(col => col.type=="SelectField").concat(current_state.groups.filter(col => col.type=="SelectField"))
        const calculatedColumnsNames = formulaColumns.map(col => col.key_name)
        const calculatedColumnsFormulas = formulaColumns
            .map(col => Object.assign({}, { [col.key_name]: col.formula }))
            .reduce((obj, curr) => {
                return { ...obj, ...curr }
            }, {})

        let contextAsScope = item => () => {
            return {
                ...formulasLib,
                ...item
            }
        }
        //columna tipo customFn, es necesario reemplazar los operandos en la formula y evaluar
        if (res.status === false) {
            throw (res.message);
        }

        const normalizeFormula = (formula) => {
            return formula.replace(/props\((.*?)\)/g, function (not_use, operand) {
                if (calculatedColumnsNames.indexOf(operand) === -1) {
                    if ((operand in fieldsMapTypes) && ["DecimalField", "FloatField"].indexOf(fieldsMapTypes[operand]) !== -1) {
                        return `(parseFloat(${operand} || 0))`
                    } else {
                        return `(${operand})`
                    }
                } else {
                    return `(${normalizeFormula(calculatedColumnsFormulas[operand], true)})`
                }
            })
        }

        const parseRes = res.data.map(item => {
            const calculatedColumns = formulaColumns.map(col => {
                const itemContext = contextAsScope(item)()
                return { [col.key_name]: (new Function(...Object.keys(itemContext), `return ${normalizeFormula(col.formula)}`))(...Object.values(itemContext)) }
            }).reduce((obj, curr) => {
                return { ...obj, ...curr }
            }, {})

            const enumColumnsValues = enumColumns.map(col => {
                let id = `Enum.${col.id_enum}.${item[col.key_name]}`;
                let description = window.ResourceFlex[window.sdkFlexCulture.id][id] || item[col.key_name];
                return { [col.key_name]: description}
            }).reduce((obj, curr) => {
                return { ...obj, ...curr }
            }, {})

            return { ...item, ...calculatedColumns, ...enumColumnsValues}
        });

        if(dispatch != null){
            dispatch(fetchPreviewDataSuccess(parseRes));
        }
        ErrorsWindow.SuccessFlex();
        return parseRes
    }).catch(error => {                    
        console.error(error);
        try {
            SDKNotification.ShowError("Custom.Generic.Message.Error");
            ErrorsWindow.ErrorsFlex(error);
        } catch (err) {
            console.error(err);
            SDKNotification.ShowError("Custom.Generic.Message.Error");
            ErrorsWindow.ErrorsFlex(err);
        }

        if(dispatch != null){
            dispatch(getConfigError(error));
        }else{
            return error;
        }
    })
    .catch(error => {
        console.error(error);
        try {
            SDKNotification.ShowError("Custom.Generic.Message.Error");
            ErrorsWindow.ErrorsFlex(error);
        } catch (err) {
            console.error(err);
            SDKNotification.ShowError("BLFlex.Notification.ErrorLoadData");
            ErrorsWindow.ErrorsFlex(err);
        }
        if(dispatch != null){
            dispatch(getConfigError(error));
        }else{
            return error;
        }
    })
    .catch(error => {
        if(dispatch != null){
            dispatch(fetchPreviewDataError(error));
        }else{
            return error;
        }            
    })
}

export const mapFilters = (filters, current_state) => {
    let new_filters = [];
    if(filters['selectedFilterOperation']){
        let field = CreateFieldFilter(filters, current_state, true);
        new_filters.push(field);
    }else{        
        for(const filter of filters){
            if (Array.isArray(filter)){
                let field = CreateFieldFilter(filter, current_state, false);
                new_filters.push(field);
            }
        }
    }

    return new_filters;
}

export const CreateFieldFilter = (filters, current_state, is_simple) => {
    if (!Array.isArray(filters[0])) {
        return createSimpleFieldFilter(filters, current_state);
    } else if (is_simple) {
        return createComplexFieldFilter(filters, current_state);
    }    
    return null;
};

const createSimpleFieldFilter = (filters, current_state) => {
    const key_name = filters[0];
    const field = current_state.columns.find((f) => f.key_name === key_name);
    const is_date = field.type === "DateField" || field.type === "DateTimeField";
    const type_filter = GetTypeFilter(filters["selectedFilterOperation"], is_date);
    const value = filters[2];

    field["equal_from"] = is_date ? new Date(value).toISOString() : value;
    field["selected_operator"] = type_filter;

    if (filters["selectedFilterOperation"] === "between") {
        const filterEqualTo = filters[2];
        const valueTo = filterEqualTo[2];
        field["to"] = is_date ? new Date(valueTo).toISOString() : valueTo;
    }

    return field;
};

const createComplexFieldFilter = (filters, current_state) => {
    const selectedFilterOperation = filters["selectedFilterOperation"];
    const filterEqualFrom = filters[0];
    const key_name = filterEqualFrom[0];
    const field = current_state.columns.find((f) => f.key_name === key_name);
    const is_date = field.type === "DateField" || field.type === "DateTimeField";
    const type_filter = GetTypeFilter(selectedFilterOperation, is_date);
    const valueFrom = filterEqualFrom[2];
  
    field["equal_from"] = is_date ? new Date(valueFrom).toISOString() : valueFrom;
    field["selected_operator"] = type_filter;
  
    if (selectedFilterOperation === "between") {
      const filterEqualTo = filters[2];
      const valueTo = filterEqualTo[2];
      field["to"] = is_date ? new Date(valueTo).toISOString() : valueTo;
    }
  
    return field;
};  

export const GetTypeFilter = (type_filter, is_date) => {
    switch (type_filter) {
      case "notcontains":
        return "not_contains";
      case "startswith":
        return "starts_with";
      case "endswith":
        return "end_with";
      case "=":
        return "equal";
      case "<>":
        return "not_equal";
      case ">":
        return is_date ? "after" : "gt";
      case "<":
        return is_date ? "before" : "lt";
      case ">=":
        return "gte";
      case "<=":
        return "lte";
      default:
        return type_filter;
    }
  };

const FilterDataCustom = (filters_custom, data) => {
    return data.filter(item => filterItem(item, filters_custom));
  };
  
const filterItem = (item, filters_custom) => {
    for (const filter of filters_custom) {
        if (!applyFilter(item, filter)) {
        return false;
        }
    }
    return true;
};
  
const applyFilter = (item, filter) => {
    const value = item[filter.key_name];
    const type_filter = filter.selected_operator;
    let value_from = filter.equal_from;
    let value_to = filter.to;
    const type = filter.type;

    if (type === "DateField" || type === "DateTimeField") {
        const dateValue = new Date(value).setSeconds(0);
        value_from = new Date(value_from).setSeconds(0);
        if (type_filter === "between") {
        value_to = new Date(value_to).setSeconds(0);
        }
        return applyDateFilter(dateValue, value_from, value_to, type_filter);
    }

    return applyNonDateFilter(value, value_from, value_to, type_filter);
};
  
const applyDateFilter = (dateValue, value_from, value_to, type_filter) => {
    switch (type_filter) {
        case "equal":
        return dateValue === value_from;
        case "not_equal":
        return dateValue !== value_from;
        case "between":
        return dateValue >= value_from && dateValue <= value_to;
        case "after":
        return dateValue >= value_from;
        case "before":
        return dateValue <= value_from;
        default:
        return false;
    }
};
  
const applyNonDateFilter = (value, value_from, value_to, type_filter) => {
    switch (type_filter) {
        case "equal":
        return value === value_from;
        case "not_equal":
        return value !== value_from;
        case "contains":
        return value.includes(value_from);
        case "not_contains":
        return !value.includes(value_from);
        case "starts_with":
        return value.startsWith(value_from);
        case "end_with":
        return value.endsWith(value_from);
        case "gt":
        return value > value_from;
        case "lt":
        return value < value_from;
        case "gte":
        return value >= value_from;
        case "lte":
        return value <= value_from;
        default:
        return false;
    }
};  
    
export const fetchPreviewData = () => {    
    return (dispatch, getState) => {
        let current_state = getState().general || {};
        if (current_state.timeout_data_id !== null && typeof (current_state.timeout_data_id) !== "undefined") {
            clearTimeout(current_state.timeout_data_id);
        }
        let url = '';
        let params = {};
        if (current_state.is_create_edit && !current_state.is_list_view) {
            url = '/api/BLFlex/PreviewData/';
            //extraer las opciones del field_list en forma segura
            const regularColumns = current_state.columns.filter(col => !col.customFn)
            //concatenar las columnas 
            params = {
                "module_path": current_state.report_header_data.module_path,
                "selected_class": current_state.report_header_data.selected_class,
                "module": current_state.report_header_data.module,
                "filters": current_state.filters,
                //"columns": current_state.columns,
                "columns": regularColumns,
                "groups": current_state.groups,
                "set_top": 'true'
            }
        } else if(current_state.is_list_view){
            url = '/api/BLFlex/PreviewData/';
            //extraer las opciones del field_list en forma segura
            const regularColumns = current_state.columns.filter(col => !col.customFn)
            current_state.id = current_state.report_header_data.id;
            //concatenar las columnas 
            params = {
                "module_path": current_state.report_header_data.module_path,
                "selected_class": current_state.report_header_data.selected_class,
                "module": current_state.report_header_data.module,
                "filters": current_state.filters,
                //"columns": current_state.columns,
                "columns": regularColumns,
                "groups": current_state.groups,
                "set_top": 'false',
                "filter_search": current_state.filter_search,
            }
            if(current_state.server_pagination){
                params.skip = current_state.skip;
                params.take = current_state.take;
            }
        } else {
            url = '/api/BLFlex/UpdateData/';
            params = {
                "id": current_state.report_header_data.id,
                "filter_list": current_state.filters,
                'current_record': (current_state.current_record ? current_state.current_record : 0)
            }
        }
        let timeoutData = setTimeout(() => {
            fetchData(url, {
                method: 'POST',
                credentials: "same-origin",
                headers: {
                    "Accept": "application/json",
                    'X-Requested-With': 'XMLHttpRequest'
                },
                body: JSON.stringify(params)
            }, current_state, dispatch);            
        }, 500);

        if(!current_state.is_list_view){
            dispatch(fetchPreviewDataPending(timeoutData));
        }else if(current_state.skip === 0){
            dispatch(fetchPreviewDataPending(timeoutData));
        }else if(!current_state.server_pagination){
            dispatch(fetchPreviewDataPending(timeoutData));
        }
    }
}

export const getDevCustomStoreAction = (self) => {
    return (dispatch, getState) => {
        let customStore = new CustomStore({
            key: "rowid",
            load: async function (loadOptions) {
              let current_state = getState().general || {};
              let skip = loadOptions.skip || 0;
              let take = loadOptions.take || 100;
              let url = '/api/BLFlex/PreviewData/'
              let params = {};
              let sort = loadOptions.sort || [];
              let filters = loadOptions.filter || [];
              let filters_general = _.cloneDeep(current_state.filters);
              let filters_custom;
              if(filters.length > 0){
                let new_filters = mapFilters(filters, current_state);
                //TODO: pendiente por realizar, filtros complejos
                filters_custom = new_filters.filter(f => f.customFn);
                new_filters = new_filters.filter(f => (f.type !== "ForeignKey" || f.type !== "SelectField") && !f.customFn);
                filters_general.push(...new_filters);
              }
              if(skip === 0 && sort.length === 0 && !current_state.change_filter_search && filters.length === 0){
                return current_state.preview_data;
              }
              let regularColumns = self.props.columns.filter(col => !col.customFn);
              regularColumns = [...regularColumns, ...current_state.groups];
              if(sort.length>0){
                let index = regularColumns.findIndex(e => e.key_name === sort[0].selector);
                if(index > -1){
                    regularColumns[index].sortType = sort[0].desc ? "desc" : "asc";
                }
              }
              //concatenar las columnas 
              params = {
                  "module_path": current_state.report_header_data.module_path,
                  "selected_class": current_state.report_header_data.selected_class,
                  "module": current_state.report_header_data.module,
                  "filters": filters_general,
                  //"columns": current_state.columns,
                  "columns": regularColumns,
                  "groups": current_state.groups,
                  "set_top": 'false',
                  "skip": skip,
                  "take": take,
                  "filter_search": current_state.filter_search,
              }
      
              let fetchParams ={
                  method: 'POST',
                  headers: {
                    "Accept": "application/json",
                    'X-Requested-With': 'XMLHttpRequest'
                  },
                  body: JSON.stringify(params)
              }
      
              var data = await fetchData(url, fetchParams, current_state);
              if(filters_custom != null && filters_custom.length > 0){
                data = FilterDataCustom(filters_custom, data);
              }
      
              return {
                data
              };    
            }
      
          });
        dispatch(getDevCustomStore(customStore));
    }
}